import css from './styles.css';

const Card = (props) => {
  const {
    children,
    ...rest // spread operator
  } = props;

  return (
    <div className={css.card} {...rest}>
      {children}
      <div className={css.button}>
        <button>Click Me!</button>
      </div>
    </div>
  );
};

export default Card;